<template>
  <div class="Order">
		<div id="header">
			<van-nav-bar 
				fixed
				title="我的订单"/>
		</div>
		<div id="content">
			<van-tabs v-model="activeKey" color="#1989fa" title-active-color="#1989fa">
				<div id="empty" v-show="this.returnUser.order.length==0"><van-empty
					image-size="100px"
				  class="custom-image"
				  :image="empty_logo"
				  description="暂无订单"
				/></div>
				<div class="order-list" style="width: 95%; margin-left: 2.5%;">
					<ul>
						<!-- <li class="order-box" v-for="item in this.$store.state.user.order">
							{{item}}
						</li> -->
						<li class="order-box" v-for="order in orderList">
							<!-- {{order}} -->
							<p class="order-header">
								订单编号：{{order.orderId}}
								<span style="float: right; color: red;">{{order.orderState}}</span>
							</p>
							<p class="order-header" style="border: none; font-size: 17px; font-weight: 600;">
								[{{order.orderTitle}}]    {{order.titleInfo}}
								<span style="float: right; color: red;">￥{{returnFloat(order.numValue)}}</span>
							</p>
							<div class="order-content" style="width: 95%; margin:2% 2.5%;">
								<p class="address-icon-title">取</p>
								<p class="pick">{{order.pickInfo}}</p>
								<p class="pick-address">{{order.pickAddress}}</p>
							</div>
							<div class="order-content" style="width: 95%; margin:4% 2.5%;">
								<p class="address-icon-title" style="background-color: #F5361C;">送</p>
								<p class="receive">{{order.receiveInfo}}</p>
								<p class="receive-address">{{order.receiveAddress}}</p>
							</div>
							<p class="order-remark">订单备注：<span>{{order.remarkInfo}}</span></p>
						</li>
						
					</ul>
				</div>
				
			  <van-tab title="全部" name="全部">
				</van-tab>
			  <van-tab title="待付款" name="待付款"></van-tab>
			  <van-tab title="待接单" name="待接单"></van-tab>
			  <van-tab title="待送单" name="待送单"></van-tab>
				<van-tab title="已完成" name="已完成"></van-tab>
			</van-tabs>
			
		</div>
		<div id="footer">
			<van-tabbar v-model="active" route>
			  <van-tabbar-item replace to="/home" name="home" icon="home">主页</van-tabbar-item>
			  <van-tabbar-item replace to="/type" name="type" icon="type">分类</van-tabbar-item>
			  <van-tabbar-item replace to="/order" name="order" icon="order">订单</van-tabbar-item>
			  <van-tabbar-item replace to="/me" name="me" icon="me">我的</van-tabbar-item>
			</van-tabbar>
		</div>
  </div>
</template>

<script>
import Vue from 'vue';
import VueRouter from 'vue-router'
import allschool from '../AllSchool.js'
import { Toast } from 'vant';

Vue.use(VueRouter).use(Toast);

export default {
  name: 'Login',
	data() {
	  return {
	    active: 'home',
			school: "江西财经大学",
			activeKey: '全部',
			empty_logo: require('../assets/img/empty.png'),
			empty: true,
			orderList: this.$store.state.user.order
	  };
	},
	methods: {
		returnFloat(value) {
			value = Number(value);
			value = value.toString();
			var num = value.split(".").length;
			if (num == 1) {
				value = value.toString() + ".00";
				return value;
			} else if (num == 2) {
				if (value.split(".")[1].length == 1) value = value.toString() + "0";
				else value = value.split(".")[0] + "." + value.split(".")[1].substring(0, 2);
				return value;
			}
		},
	},
	computed: {
		returnUser(){
			this.$store.commit('userInit');
			return this.$store.state.user;
		}
	}
}
</script>

